.stats-section {
  margin-bottom: 2rem;
}
.stats-section .stats-cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
  margin-bottom: 1.5rem;
}
.stats-section .stats-cards .stat-card {
  position: relative;
  background: #ffffff;
  border-radius: 14px;
  padding: 16px 18px;
  border: 1px solid #e5e7eb;
  box-shadow: 0 4px 16px rgba(2, 6, 23, 0.05);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}
.stats-section .stats-cards .stat-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(2, 6, 23, 0.08);
}
.stats-section .stats-cards .stat-card .card-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
}
.stats-section .stats-cards .stat-card .title {
  font-size: 14px;
  font-weight: 600;
  color: #334155;
}
.stats-section .stats-cards .stat-card .badge {
  background: rgba(59, 130, 246, 0.12);
  border-radius: 10px;
  padding: 6px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.stats-section .stats-cards .stat-card .badge-icon {
  width: 16px;
  height: 16px;
  color: #2563eb;
}
.stats-section .stats-cards .stat-card .value {
  font-size: 28px;
  font-weight: 800;
  color: #0f172a;
  margin: 2px 0 6px;
}
.stats-section .stats-cards .stat-card .subtitle {
  font-size: 13px;
  color: #6b7280;
}
.stats-section .stats-cards .stat-card.theme-blue {
  background: #f0f7ff;
  border-color: #dbeafe;
}
.stats-section .stats-cards .stat-card.theme-blue .badge {
  background: rgba(59, 130, 246, 0.15);
}
.stats-section .stats-cards .stat-card.theme-green {
  background: #effcf4;
  border-color: #d1fae5;
}
.stats-section .stats-cards .stat-card.theme-green .badge {
  background: rgba(16, 185, 129, 0.15);
}
.stats-section .stats-cards .stat-card.theme-orange {
  background: #fff7ed;
  border-color: #fed7aa;
}
.stats-section .stats-cards .stat-card.theme-orange .badge {
  background: rgba(245, 158, 11, 0.15);
}
.stats-section .stats-cards .stat-card.theme-purple {
  background: #f6f3ff;
  border-color: #ddd6fe;
}
.stats-section .stats-cards .stat-card.theme-purple .badge {
  background: rgba(139, 92, 246, 0.15);
}
.stats-section .create-button {
  background: #2563eb;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 0.875rem 1.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  white-space: nowrap;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.stats-section .create-button:hover {
  background: #1d4ed8;
  transform: translateY(-1px);
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}
.stats-section .create-button:active {
  transform: translateY(0);
}
.stats-section .create-button .icon {
  font-size: 0.875rem;
}

@media (max-width: 1024px) {
  .stats-section .stats-cards {
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }
}
@media (max-width: 768px) {
  .stats-section .stats-cards {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  .stats-section .stats-cards .stat-card {
    padding: 16px;
  }
}
